import React from "react";
import { Card, Popover } from "antd";
import { GithubOutlined, GoogleOutlined } from "@ant-design/icons";
function AntCard(props) {
  const { Meta } = Card;
  const dispo = props.disponible;
  const contentGithub = (
    <div>
      {dispo ? (
        <a href={props.github} target="_blank" rel="noreferrer">
          Github
        </a>
      ) : (
        <p>Desolé ce repository est privée</p>
      )}
    </div>
  );
  const contentSite = (
    <div>
      <a href={props.content} target="_blank" rel="noreferrer">
        {props.titleContent}
      </a>
    </div>
  );
  return (
    <Card
      hoverable={true}
      style={{
        width: 300,
        boxShadow: "0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23)",
        margin: "10px",
      }}
      cover={<img alt="example" src={props.image} style={{ height: 200 }} />}
    >
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          marginBottom: "10px",
        }}
      >
        <Meta title={props.title} />
        <div
          style={{
            width: "20%",
            display: "flex",
            justifyContent: "space-between",
          }}
        >
          <Popover content={contentSite} title="Decouvrir">
            <GoogleOutlined />
          </Popover>
          <Popover content={contentGithub} title="Github">
            <GithubOutlined />
          </Popover>
        </div>
      </div>
      <Meta description={props.description} />

      <div></div>
    </Card>
  );
}

export default AntCard;
